import { graphql } from 'gatsby'
import _ from 'lodash'
import React from 'react'
import { CoverImage } from '../components/cover-image'
import aboutCoverImage from '../../content/assets/services-cover.jpg'
import { Link } from 'gatsby'
import { Head } from '../components/head'
import { HOME_TITLE } from '../constants'
import { Layout } from '../layout'
import '../styles/style.scss'
import '../styles/mobile-style.scss'
import './services.scss'
 
const Services = ({ data, location }) => {
  const { siteMetadata } = data.site;

  return (
    <Layout location={location} title={siteMetadata.title}>
        <Head title={HOME_TITLE} keywords={siteMetadata.keywords} />
        <CoverImage image={aboutCoverImage} />
        <div className="services-page">
            <div className="services-page__intro">
                <h1 className="services-page__intro--title">Services</h1>
                <p>At bold, which is Ordina Belgium's digital experience unit, we focus on delivering a good customer experience by gaining insights through research, designing to the latest standards and developing in cutting edge technology. These domains are all incorporated into our new solution, <Link activeClassName='active' to={`/design-system`} className='navigation-item' partiallyActive={true}> Design Systems</Link>.
                </p>
            </div>
        </div>
    </Layout>
  )
}

export default Services;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
